<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <p>{{ `${user.userName} ${user.userSex ? "" : ""}` }}</p>
    </header>
    <!--  -->
    <ul class="form-box">
      <li>
        <div class="title"></div>
        <div class="content">
          <p v-if="balance != null">{{ balance }}</p>
          <div v-else class="createWallet" @click="createWallet"></div>
        </div>
      </li>
      <li>
        <div class="title"></div>
        <div class="content">
          <input v-model="money" placeholder="" />
        </div>
      </li>
      <li>
        <div class="title"></div>
        <div class="content">
          <p>{{ creditNum }}</p>
        </div>
      </li>
    </ul>
    <div class="button-login">
      <button @click="recharge">
        {{
          creditEarningByRecharge == 0
            ? ""
            : "" + creditEarningByRecharge + ""
        }}
      </button>
    </div>

    <div class="button-register">
      <button @click="withdrawMoney"></button>
    </div>
    <div class="button-register">
      <button @click="router2tans"></button>
    </div>
    <div class="button-login" v-if="creditEarningBySign != 0">
      <button @click="sign(user.userId, creditEarningBySign)">
        {{ creditEarningBySign }}
      </button>
    </div>
    <div class="button-register" v-else>
      <button></button>
    </div>
    <div style="height: 16vw"></div>

    <!--  -->
    <Footer></Footer>
  </div>
</template>
<script>
import Footer from "../components/Footer.vue";

export default {
  name: "Me",
  data() {
    return {
      creditNum: 0,
      creditEarningBySign: 0,
      creditEarningByRecharge: 0,
      balance: 0,
      userId: "",
      money: "",
      reg: /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/,
    };
  },
  watch: {
    money: function (val) {
      // this.queryEarnCreditByRecharge(this.user.userId,this.money);
      if (this.reg.test(val)) {
        this.queryEarnCreditByRecharge(this.user.userId, val);
      }
      if (val == "") {
        this.creditEarningByRecharge = 0;
      }
    },
  },
  created() {
    this.user = this.$getSessionStorage("user");
    this.queryBalance();
    this.queryAvailableCredit(this.user.userId);
    this.queryEarningCreditBySign(this.user.userId);
  },
  methods: {
    queryBalance() {
      //
      // console.log("",this.user);
      if (this.user.walletId == null) {
        this.balance = null;
      } else {
        let url = "VirtualWalletController/queryBalance/" + this.user.walletId;
        this.$axios
          .get(url)
          .then((response) => {
            this.balance = response.data.result;
          })
          .catch((error) => {
            console.error(error);
          });
      }
    },
    queryAvailableCredit(userId) {
      //
      let url = "CreditController/getTotalCredit/" + userId;
      this.$axios
        .get(url)
        .then((response) => {
          this.creditNum = response.data.result;
        })
        .catch((error) => {
          console.error(error);
        });
    },
    queryEarningCreditBySign(userId) {
      //
      let url = "CreditController/queryEarningCreditBySign/" + userId;
      this.$axios
        .get(url)
        .then((response) => {
          this.creditEarningBySign = response.data.result;
        })
        .catch((error) => {
          console.error(error);
        });
    },
    queryEarnCreditByRecharge(userId, money) {
      //
      money = Math.floor(money);
      let url =
        "CreditController/queryEarnCreditByRecharge/" + userId + "/" + money;
      this.$axios
        .get(url)
        .then((response) => {
          this.creditEarningByRecharge = Math.floor(response.data.result);
        })
        .catch((error) => {
          console.error(error);
        });
    },
    sign(userId, creditNum) {
      //
      let url = "CreditController/earnCreditBySign/" + userId + "/" + creditNum;
      this.$axios
        .put(url)
        .then((response) => {
          if (response.data.result == 1) {
            this.queryEarningCreditBySign(userId);
            this.queryAvailableCredit(userId);
          }
        })
        .catch((error) => {
          console.error(error);
        });
    },
    createWallet() {
      //
      let url_1 =
        "VirtualWalletController/userCreateVirtualWallet/" + this.user.userId;
      this.$axios
        .post(url_1)
        .then((response) => {
          this.user.walletId = response.data.result;
          this.$setSessionStorage("user", this.user);
          // 
          // console.log("after create wallet")
          // console.log("",this.user)
          let url_2 =
            "VirtualWalletController/queryBalance/" + this.user.walletId;
          this.$axios
            .get(url_2)
            .then((response) => {
              this.balance = response.data.result;
            })
            .catch((error) => {
              console.error(error);
            });
        })
        .catch((error) => {
          console.error(error);
        });
    },
    router2tans() {
      //
      if (this.user.walletId == null || this.balance == null) {
        alert("!!");
      } else {
        this.$router.push({ path: "/transaction" });
      }
    },
    recharge() {
      //
      if (!this.reg.test(this.money)) {
        alert("!");
      } else if (this.user.walletId == null || this.balance == null) {
        alert("!!");
      } else {
        if (this.creditEarningByRecharge == 0) {
          let url_1 =
            "VirtualWalletController/recharge/" +
            this.user.walletId +
            "/" +
            this.money;
          this.$axios
            .put(url_1)
            .then((response) => {
              if (response.data.result == 1) {
                alert("");
                this.queryBalance();
              } else {
                alert("");
              }
              this.money = 0;
            })
            .catch((error) => {
              console.error(error);
            });
        } else {
          let url_2 =
            "CreditController/earnCreditBySignAndRecharge/" +
            this.user.userId +
            "/" +
            this.user.walletId +
            "/" +
            Math.floor(this.money) +
            "/" +
            this.creditEarningByRecharge;
          this.$axios
            .put(url_2)
            .then((response) => {
              if (response.data.result == 1) {
                alert("");
                this.queryBalance();
                this.queryAvailableCredit(this.user.userId);
              } else {
                alert("");
              }
              this.money = 0;
            })
            .catch((error) => {
              alert(",");
              console.error(error);
            });
        }
      }
    },
    withdrawMoney() {
      //
      if (!this.reg.test(this.money)) {
        alert("!");
      } else if (this.user.walletId == null || this.balance == null) {
        alert("!!");
      } else if (this.money > this.balance) {
        alert("");
      } else {
        let url =
          "VirtualWalletController/withdrawMoney/" +
          this.user.walletId +
          "/" +
          this.money;
        this.$axios
          .put(url)
          .then((response) => {
            if (response.data.result == 1) {
              alert("");
              this.queryBalance();
            } else {
              alert("");
            }
            this.money = 0;
          })
          .catch((error) => {
            console.error(error);
          });
      }
    },
  },
  components: {
    Footer,
  },
};
</script>

<style scoped>
/******************  ******************/
.wrapper {
  width: 100%;
  height: 100%;
}

/****************** header ******************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: deepskyblue;

  position: fixed;
  top: 0;
  left: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  color: white;
  font-size: 5vw;
  font-weight: 700;
}

/******************  ******************/
.wrapper .form-box {
  width: 100%;
  padding-top: 12vw;
}

.wrapper .form-box li {
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
  display: flex;
  align-items: center;
}

.wrapper .form-box li .title {
  flex: 0 0 25vw;
  font-size: 3.5vw;
  color: darkslategray;
  font-weight: 700;
}

.wrapper .form-box li .content {
  flex: 1;
  font-size: 3.5vw;
  color: darkslategray;
}

.wrapper .form-box li .content .createWallet {
  width: 28%;
  background-color: #f90;
  color: white;
  user-select: none;
  cursor: pointer;

  box-sizing: border-box;
  padding: 1vw 2vw;
  border-radius: 3vw;
}

.wrapper .form-box li .content input {
  width: 100%;
  box-sizing: border-box;
  padding: 2vw 2vw;
  border-radius: 3vw;
  font-size: 3vw;

  border: none;
  outline: none;
}

.wrapper .form-box li .content p {
  margin-left: 1vw;
}

/******************  ******************/
.wrapper .button-login {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw 10vw 0;
}

.wrapper .button-login button {
  width: 100%;
  height: 10vw;
  background-color: mediumseagreen;
  border: none;
  outline: none;
  border-radius: 4vw;

  font-size: 3.5vw;
  color: white;

  cursor: pointer;
}

.wrapper .button-register {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw 10vw 0;
  display: flex;
  justify-content: center;
}

.wrapper .button-register button {
  width: 100%;
  height: 10vw;
  background-color: white;
  border: none;
  outline: none;
  border-radius: 4vw;
  border: solid 1.5px #ddd;

  font-size: 3.5vw;
  color: darkslategray;

  cursor: pointer;
}
</style>
